___CSS_LOADER_EXPORT___.push([module.id, ".main-wrapper{background-color:#eaeaea;font-family:Nunito,sans-serif}.v-btn:not(.v-btn--round).v-size--default{height:40px;min-width:64px;padding:0 16px}.v-messages{flex:1 1 auto;font-size:10px;height:12px;min-width:1px;position:relative}.v-text-field__details{display:flex;flex:1 0 auto;max-height:12px;max-width:100%;overflow:hidden}.v-text-field.v-text-field--enclosed .v-text-field__details{margin-bottom:0;padding-top:0}.v-text-field .v-input__control,.v-text-field .v-input__slot,.v-text-field fieldset{border-radius:.375rem}", "",{"version":3,"sources":["webpack://./resources/js/Layouts/InventarioLayout.vue"],"names":[],"mappings":"AA6IA,cAEA,wBAAA,CADA,6BAEA,CAEA,0CACA,WAAA,CACA,cAAA,CACA,cACA,CAEA,YACA,aAAA,CACA,cAAA,CACA,WAAA,CACA,aAAA,CACA,iBACA,CAEA,uBACA,YAAA,CACA,aAAA,CAEA,eAAA,CADA,cAAA,CAEA,eACA,CAEA,4DAEA,eAAA,CADA,aAEA,CAEA,oFAGA,qBACA","sourcesContent":["<template>\r\n    <v-app class=\"app-wrapper\">\r\n        <v-app-bar color=\"white\" app>\r\n\r\n\r\n            <v-btn text color=\"primary\" @click=\"irConciliacion\">\r\n                Conciliacion\r\n            </v-btn>\r\n\r\n            <v-spacer />\r\n\r\n            <v-menu offset-y>\r\n                <template v-slot:activator=\"{ attrs, on }\">\r\n                    <v-btn text color=\"primary\" class=\"\" v-bind=\"attrs\" v-on=\"on\">\r\n                        {{ user.nombres }}\r\n                        <v-icon right>mdi-account </v-icon>\r\n                    </v-btn>\r\n                </template>\r\n\r\n                <v-list dense>\r\n                    <v-subheader>Opciones</v-subheader>\r\n                    <v-list-item-group v-model=\"selectedMenu\" color=\"primary\">\r\n                        <v-list-item v-for=\"(item, i) in items\" :key=\"i\" @click=\"SelectMenu(item.text)\">\r\n\r\n                            <v-list-item-icon>\r\n                                <v-icon v-text=\"item.icon\"></v-icon>\r\n                            </v-list-item-icon>\r\n                            <v-list-item-content>\r\n                                <v-list-item-title v-text=\"item.text\"></v-list-item-title>\r\n                            </v-list-item-content>\r\n\r\n                        </v-list-item>\r\n                    </v-list-item-group>\r\n                </v-list>\r\n            </v-menu>\r\n        </v-app-bar>\r\n\r\n        <v-main class=\"main-wrapper\">\r\n            <slot />\r\n        </v-main>\r\n\r\n        <!-- <ReLogin :user=\"user\"  :dialog=\"dialog_relogin\"  @dialog=\"dialog_relogin = $event\" />-->\r\n\r\n        <v-dialog v-model=\"dialog_change_pass\" persistent :overlay=\"false\" max-width=\"400px\">\r\n            <v-card>\r\n                <v-card-title class=\"text-h6\">\r\n                    Cambiar contraseña\r\n                </v-card-title>\r\n\r\n                <v-form class=\"px-5 py-4\" ref=\"form\" v-model=\"valid\" lazy-validation>\r\n                    <v-text-field v-model=\"password\" :append-icon=\"show_password ? 'mdi-eye' : 'mdi-eye-off'\"\r\n                        :rules=\"[rules.required, rules.min]\" :type=\"show_password ? 'text' : 'password'\"\r\n                        label=\"Nueva Contraseña\" counter @click:append=\"show_password = !show_password\"></v-text-field>\r\n\r\n                    <v-btn :disabled=\"!valid\" color=\"primary\" class=\"mt-1\" block :loading=\"loading_btn\"\r\n                        @click=\"ChangePassword()\">\r\n                        Guardar Contraseña\r\n                    </v-btn>\r\n                </v-form>\r\n            </v-card>\r\n        </v-dialog>\r\n    </v-app>\r\n</template>\r\n\r\n<script>\r\nimport ReLogin from \"../components/ReLogin.vue\";\r\nexport default {\r\n    components: { ReLogin },\r\n    data: () => ({\r\n        dialog_relogin: false,\r\n\r\n        items: [\r\n            { text: \"Perfil\", icon: \"mdi-account\" },\r\n            { text: \"Cargos\", icon: \"mdi-file-document\", to:\"url\" },\r\n            { text: \"Conciliar\", icon: \"mdi-file-document\", to:\"url\" },\r\n            { text: \"Salir\", icon: \"mdi-power\" },\r\n        ],\r\n        selectedMenu: null,\r\n        focus: false,\r\n        blur: false,\r\n        session: true,\r\n\r\n        //cambair contraseña\r\n        dialog_change_pass: false,\r\n        show_password: false,\r\n        password: \"\",\r\n        valid: true,\r\n        loading_btn: false,\r\n\r\n        rules: {\r\n            required: (value) => !!value || \"Requerido.\",\r\n            min: (v) => v.length >= 4 || \"Min 4 caracteres\",\r\n        },\r\n        //cambair contraseña\r\n    }),\r\n    computed: {\r\n        user() {\r\n            return this.$page.props.auth.user;\r\n        },\r\n    },\r\n    async created() {\r\n        this.dialog_change_pass = !this.user.estado_password;\r\n    },\r\n    methods: {\r\n        async ChangePassword() {\r\n            if (this.$refs.form.validate()) {\r\n                try {\r\n                    this.loading_btn = true;\r\n                    let res = await axios.post(\"/inventario/update-password\", {\r\n                        password: this.password,\r\n                    });\r\n\r\n                    this.dialog_change_pass = false;\r\n                    this.loading_btn = false;\r\n                } catch (error) {\r\n\r\n                    this.loading_btn = false;\r\n                }\r\n            }\r\n        },\r\n        SelectMenu(menu) {\r\n            if (menu == \"Perfil\") {\r\n                this.$inertia.get(\"/inventario/perfil\");\r\n            } else if (menu == \"Cargos\") {\r\n                this.$inertia.get(\"/inventario/cargos\")\r\n            } else if (menu == \"Conciliar\") {\r\n                this.$inertia.get(\"/inventario/conciliacion\")\r\n            } else if (menu == \"Salir\") {\r\n                this.$inertia.post(\"/logout\");\r\n            }\r\n        },\r\n        irConciliacion() {\r\n            this.$inertia.get(\"/inventario/conciliacion\");\r\n        }\r\n    },\r\n};\r\n</script>\r\n\r\n<style>\r\n@import url(\"https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&display=swap\");\r\n\r\n.main-wrapper {\r\n    font-family: \"Nunito\", sans-serif;\r\n    background-color: #eaeaea;\r\n}\r\n\r\n.v-btn:not(.v-btn--round).v-size--default {\r\n    height: 40px;\r\n    min-width: 64px;\r\n    padding: 0 16px;\r\n}\r\n\r\n.v-messages {\r\n    flex: 1 1 auto;\r\n    font-size: 10px;\r\n    height: 12px;\r\n    min-width: 1px;\r\n    position: relative;\r\n}\r\n\r\n.v-text-field__details {\r\n    display: flex;\r\n    flex: 1 0 auto;\r\n    max-width: 100%;\r\n    max-height: 12px;\r\n    overflow: hidden;\r\n}\r\n\r\n.v-text-field.v-text-field--enclosed .v-text-field__details {\r\n    padding-top: 0;\r\n    margin-bottom: 0px;\r\n}\r\n\r\n.v-text-field .v-input__control,\r\n.v-text-field .v-input__slot,\r\n.v-text-field fieldset {\r\n    border-radius: 0.375rem;\r\n}\r\n</style>"],"sourceRoot":""}]);
